import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	components: {uniLoadMore},
	data(){
		return{
			money:"",//交易中
			money1:"",//结算中
			srcArr:["","../../static/pro/shop1@2x.png","","../../static/pro/shop3@2x.png","","","","../../static/pro/shop7@2x.png"],
			dataList:[],//数据
			type:"1",//当前类型1为交易中2为结算中
			status:"loading",
			shopObj:{},
			pageNo:1,
			pageAll:2,
		}
	},
	onReachBottom(){
		this.search()
	},
	methods:{
		change(num){
			this.type=num;
			this.dataList=[];
			this.pageNo=1;
			this.pageAll=2;
			this.status="loading";
			this.search()
		},
		search(){
			let that=this;
			let url="";
			if(that.type==1){
				url="/fund/searchSalesRevenueForPage"
			}else{
				url="/fund/searchTransactionIngForPage"
			}
			console.log(url)
			if(that.pageNo<=that.pageAll){
				that.$ajax({
					url:url,
					data:{
						pageNo:that.pageNo,
						pageSize:20,
						searchData: {
						  marchantId: that.shopObj.merchantId
						},
					},
					success(d){
						that.pageNo=that.pageNo*1+1;
						that.pageAll=d.totalPage
						console.log(d);
						if(that.pageNo==2){
							that.dataList=d.lists
						}else{
							let arr=that.dataList;
							that.dataList=that.dataList.concat(d.lists)
						}
						if(that.pageNo>that.pageAll){
							that.status="noMore"
						}
					}
				})
			}
		}
	},
	onLoad(e){
		this.type=e.type;
		this.money=e.money||0;
		this.money1=e.money1||0;
	},
	onShow(){
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
				that.search()
			}
		})
	}
}
